.success {
  position: absolute;
  height: 40vh;
  border: 1px solid white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: #3127274f;
  backdrop-filter: blur(5px);

  svg {
    width: 20vw;
    height: 20vh;
    fill: #0ec143;
    margin-bottom: 2vh;
    stroke: #0ec143;
    animation: svh 3s ease forwards;

    path {
      stroke: #0ec143;
    }
  }

  p {
    font-size: 3vh;
    text-shadow: 1px 1px 1px black;
  }
}

.close {
  position: absolute;
  right: 2%;
  top: 3%;
  font-size: 4vh;
  color: rgb(219, 61, 61);
  cursor: pointer;
}

.facesvg path {
  stroke-dasharray: 2000;
  stroke-dashoffset: 2000;
  stroke: white;
  fill: transparent;
  animation: face 3s linear forwards, facebg 1s linear forwards;
  animation-delay: 0s, 2.8s;
}

@keyframes face {
  to {
    stroke-dashoffset: 0;
  }
}

@keyframes facebg {
  to {
    fill: rgba(245, 245, 245, 0.11);
    stroke: transparent;
  }
}

.loader {
  background: #ffffff;
  -webkit-animation: load1 1s infinite ease-in-out;
  animation: load1 1s infinite ease-in-out;
  width: 1em;
  height: 4em;

  &:before,
  &:after {
    background: #ffffff;
    -webkit-animation: load1 1s infinite ease-in-out;
    animation: load1 1s infinite ease-in-out;
    width: 1em;
    height: 4em;
  }

  color: #ffffff;
  text-indent: -9999em;
  margin: 88px auto;
  position: relative;
  font-size: 11px;
  -webkit-transform: translateZ(0);
  -ms-transform: translateZ(0);
  transform: translateZ(0);
  -webkit-animation-delay: -0.16s;
  animation-delay: -0.16s;
  margin-top: 50vh;

  &:before,
  &:after {
    position: absolute;
    top: 0;
    content: "";
  }

  &:before {
    left: -1.5em;
    -webkit-animation-delay: -0.32s;
    animation-delay: -0.32s;
  }

  &:after {
    left: 1.5em;
  }
}

@-webkit-keyframes load1 {
  0%,
  80%,
  100% {
    box-shadow: 0 0;
    height: 4em;
  }

  40% {
    box-shadow: 0 -2em;
    height: 5em;
  }
}

@keyframes load1 {
  0%,
  80%,
  100% {
    box-shadow: 0 0;
    height: 4em;
  }

  40% {
    box-shadow: 0 -2em;
    height: 5em;
  }
}

.mainServ {
  display: flex;
  flex-direction: column;
  background: url("./img/pruebas5000.svg") no-repeat;
  background-size: cover;
  justify-content: center;
  align-items: center;
  height: 70vh;
  border-bottom: 3px solid #ffd19b;

  .titles {
    margin-top: 6vh;
  }
}

.titles {
  font-size: 6vh;
}

.formfrt {
  position: absolute;
  transform: translate(-50%, -50%);
  left: 50%;
}

@media screen and(max-width:800px) {
  .mainServ {
    height: auto;
  }
}
